.gpt__navbar {
    background-image: linear-gradient(to right, #133363, #0f2443, #040C18);
    display: flex;
    height: 15vh;

}
.gpt__navbar-links_container{
    margin-left: 20px;
}

.gpt__navbar-link {
    flex: 1;
    justify-content: flex-start;
    align-items: center;
    display: flex;
}

.gpt__navbar-link-logo {
    margin-left: 2rem;
    color: white;
    margin-top: -0.5rem;
}

.gpt__navbar-link-logo {
    width: 72.56px;
    height: 19.02px;

}

.gpt__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt__navbar-links_container {
    margin-left: 4rem;

}

.gpt__navbar-links_container p,
.gpt__navbar-sign {
    color: white;
    font-weight: 700;
    font-size: 18px;
    line-height: 55px;
    text-transform: capitalize;
    margin: 1rem;
    cursor: pointer;
    display: flex;
    margin-left: 1rem;
}

.logo_name {
    font-weight: 600;
    font-size: 26px;
    line-height: 28px;
    text-transform: capitalize;
    text-align: center;
    align-items: center;
    color: orangered;
    margin-top: -2px;

}
