.gpt_button{
    margin-left: 18px;
    padding: 6px;
    width: 13vh;
    font-size: 16px;
    font-weight: 500;
    background-color: orangered;
}
.gpt_button:hover{
    background-color:#fff;
    color:  orangered;
    border-color:  orangered;
}
.npt_button{
    margin-left: 18px;
    padding: 6px;
    width: 13vh;
    font-size: 16px;
    font-weight: 500;
    background-color: white;
    color: orangered;
   }
   .npt_button:hover{
    background-color: orangered;
    color: #fff;
    border-color: #fff;
   }
.gpt__navbar-links_container-sign{
    display: none;
}

@media screen and (max-width:1050px) {
    .gpt__navbar-links_container{
        display: none;
    }

    .gpt__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width:700px){
    .gpt__navbar{
        padding: 2rem 4rem;
    }

    .gpt__navbar-sign{
        display: none;
    }
}
